<template>
	<div>
		<el-container>
			<el-header>
				已开播的房间
			</el-header>
			<el-main>
				<el-table :data="tableData" style="width: 100%">
					<el-table-column prop="date" label="日期" width="180px;">
					</el-table-column>
					<el-table-column prop="name" label="姓名" width="150px;">
					</el-table-column>
					<el-table-column prop="address" label="房间地址" >
					</el-table-column>
					<el-table-column fixed="right" label="操作">
						<template slot-scope="scope">
							<el-button @click="" type="text" size="small">进入该直播间</el-button>
							<el-button @click="" type="text" size="small">关闭该直播间</el-button>
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="标记">
						<template slot-scope="scope">
							<el-tag :type="scope.row.tag === 'danger' ? 'danger' : 'success'" close-transition>{{scope.row.tag}}</el-tag>
						</template>
					</el-table-column>
				</el-table>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [{
					date: '2016-05-02',
					name: '王虎',
					address: 'https://xxxx.com/room/123',
					tag: 'danger',
				}, {
					date: '2016-05-04',
					name: '王小',
					address: 'https://xxxx.com/room/1235',
					tag: 'danger',
					
				}, {
					date: '2016-05-01',
					name: '小虎',
					address: 'https://xxxx.com/room/1273',
					tag: 'danger',
					
				}, {
					date: '2016-05-03',
					name: '王小虎',
					address: 'https://xxxx.com/room/6123',
					tag: 'no problem',					
				}]
			};
		}
	}
</script>

<style lang="css" scoped>
	.el-header {
		text-align: center;
		line-height: 60px;
	}
</style>


