<template>
	<div>
		<el-container v-show="isList">
			<el-header>作业列表</el-header>
			<el-main>
				<div class="class-head">
					<el-input v-model="input" placeholder="请输入课程名称"></el-input>
					<el-button type="success">搜索</el-button>
					<el-button type="primary">添加作业</el-button>
				</div>
				<el-table :data="tableData" style="width: 100%">
					<el-table-column prop="name" label="作业名称" width="180">
					</el-table-column>
					<el-table-column prop="start_time" label="开始时间">
					</el-table-column>
					<el-table-column prop="stop_time" label="截止时间">
					</el-table-column>
					<el-table-column prop="join" label="操作">
					</el-table-column>
				</el-table>
			</el-main>
		</el-container>
		<!--  -->
		<el-container v-show="!isList">
			<el-header>作业反馈</el-header>
			<el-main>
				<div class="chartLine">
					<div style="float: left;margin:20px 50px">
						<p>总人数</p>
						<pie :width="400" :height="300"></pie>
					</div>
					<div style="float: right;margin:20px 50px">
						<p>作业错题统计</p>
						<line1 :width="400" :height="300"></line1>
					</div>
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import Line1 from './../chart/Line.js'
	import Pie from './../chart/Pie.js'
	
	export default {
		components: {
			Line1,
			Pie
	    },
		data() {
			return {
				isList: 'true',
				input:'',
				tableData: [{
					name: 'C++程序设计',
					start_time: '2016-01-01',
					stop_time: '2016-01-02',
					join: '停止'
				}, {
					name: 'JAVA程序设计',
					start_time: '2016-01-01',
					stop_time: '2016-01-02',
					join: '停止'
				}]
			};
		},
		created () {
			this.changeTableData()
		},
		watch: {
			"$route": "changeTableData"
		},
		methods: {
			changeTableData() {
				if (this.$route.fullPath == '/orga/hwlist') {
					this.isList = 'true'									
				}else if(this.$route.fullPath == '/orga/hwcomments'){	
					this.isList = !this.isList
				}
			}
		}
	}
</script>

<style lang="css" scoped>
	.el-main {
		background-color: #E9EEF3;
		color: #333;
		/* text-align: center;
	    line-height: 160px; */
	}
	.el-header,
	.el-footer {
		background-color: #B3C0D1;
		color: #333;
		text-align: center;
		line-height: 60px;
	}
	.class-head {
		margin-bottom: 20px;
	}
	.el-input {
		width: 30%;
		margin-right: 10px;
	}
	.chartLine {
		height: 100%;
		width: 100%;
		display: block;
	}
</style>


