<template>
  <div class="allActive">
    <el-card class="box-card">
      <h3>列表</h3>
      <el-row type="flex" align="middle" :gutter="20" style="padding:20px 0;">
        <el-col :span="5" style="width: 160px;text-align: center;">
          已选择{{ activeNum }}个
        </el-col>
        <el-col :span="5" style="width: 140px;">
          <el-select v-model="currentType" placeholder="请选择分类">
            <el-option v-for="type in types" :value="type">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="13">
          <el-button :plain="true" @click.native="handleRemove" type="danger">删除</el-button>
          <el-button :plain="true" @click.native="handleMoveToTop" type="info">置顶</el-button>
        </el-col>
      </el-row>
      <el-table :data="filteredTableData" style="width: 100%" @cell-click="handleSelect" @selection-change="selectionchange">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column property="title" label="名称" width='600px;'></el-table-column>
        <el-table-column property="time" label="时间"></el-table-column>
        <el-table-column property="category" label="所属分类"></el-table-column>
      </el-table>
      <el-row type="flex" justify="end" style="padding:20px 0; ">
        <el-pagination :current-page="5" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="sizes, prev, pager, next" :total="1000">
        </el-pagination>
      </el-row>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: 'allActive',
    data: function() {
      return {
        totalActiveNum: 3,
        totalSignUp: 204,
        auditNum: 15,
        activeNum: 0,
        currentType: '全部',
        selectItems: [],
        types: ['全部', '垃圾箱', '已读', '未读'],
        tableData: [{
          title: '近期服务器维护,请周知！',
          time: '2018-03-13 12:10:28',
          category: '系统消息'
        }, {
          title: '近期服务器维护,请周知！',
          time: '2018-03-13 12:10:28',
          category: '系统消息'
        }, {
          title: '近期服务器维护,请周知！',
          time: '2018-03-13 12:10:28',
          category: '系统消息'
        }, {
          title: '近期服务器维护,请周知！',
          time: '2018-03-13 12:10:28',
          category: '系统消息'
        }, {
          title: '近期服务器维护,请周知！',
          time: '2018-03-13 12:10:28',
          category: '系统消息'
        }, {
          title: '近期服务器维护,请周知！',
          time: '2018-03-13 12:10:28',
          category: '系统消息'
        }],
      }
    },
    computed: {
      filteredTableData: function() {
        var type = this.currentType;
        return this.tableData.filter(function(data) {
          if (type == '全部' || type == '') {
            return true
          } else {
            return data.type == type
          }
        })
      }
    },
    methods: {
      handleSelect: function(row, column, cell, event) {
        if (column.label == '操作') {
          this.$router.push('/activeManage/detail/page1');
        } else if (column.type == 'selection') {
          row.$info = !row.$selected;
        } else {
          row.$selected = !row.$selected;
          row.$info = row.$selected;
        }
      },
      selectionchange: function(val) {
        var arr = [];
        val.forEach(function(item) {
          arr.push(item.id);
        });
        this.selectItems = arr;
        this.activeNum = this.selectItems.length;
      },
      handleRemove: function() {
        var tableData = this.tableData;
        this.selectItems.forEach(function(id) {
          tableData.forEach(function(data) {
            if (id == data.id) {
              tableData.splice(tableData.indexOf(data), 1)
            }
          })
        });
        this.selectItems = [];
      },
    }
  }
</script>

<style>
  .allActive>.head>.el-col>.el-col {
    padding: 20px 0;
    border-right: solid 1px #fff;
  }
  .allActive .head {
    text-align: center;
    color: #fff;
    font-size: 30px;
    margin-bottom: 50px;
  }
  .allActive .head span {
    font-size: 16px;
  }
</style>


