<style lang="css" scoped>
	.Course {
		height: 100%;
		width: 100%;
	}
	.class-head {
		margin-bottom: 20px;
	}
	.el-input {
		width: 30%;
		margin-right: 10px;
	}
	.el-pagination {
		margin-top: 300px;
		text-align: center;
	}
</style>

<template>
	<div class="Exam">
		<div v-show="isPublishExam">
			<div class="class-head">
				<el-input v-model="input" placeholder="请输入课程名称"></el-input>
				<el-button type="success">搜索</el-button>
				<el-button type="primary">添加考试</el-button>
			</div>
			<el-table :data="tableData" style="width: 100%">
				<el-table-column prop="name" label="考试名称" width="180">
				</el-table-column>
				<el-table-column prop="start_time" label="考试时间">
				</el-table-column>
				<el-table-column prop="stop_time" label="截止时间">
				</el-table-column>
				<el-table-column prop="join" label="操作">
				</el-table-column>
			</el-table>
			<el-pagination layout="prev, pager, next" :total="10"></el-pagination>
		</div>
		<div v-show="!isPublishExam">
			<el-container>
				<el-header>考试反馈</el-header>
				<el-main>
					<div class="chartLine">
						<div style="float: left;margin:20px 50px">
							<p>总人数</p>
							<pie :width="400" :height="300"></pie>
						</div>
						<div style="float: right;margin:20px 50px">
							<p>考试错题统计</p>
							<Bar :width="400" :height="300"></Bar>
						</div>
						<div style="float: left;margin:20px 50px">
							<p>雷达图能力分析</p>
							<Radar :width="400" :height="300"></Radar>
						</div>
						<div style="float: right;margin:20px 50px">
							<p>错题分析</p>
							<Line1 :width="400" :height="300"></Line1>
						</div>
					</div>
				</el-main>
			</el-container>
		</div>
	</div>
</template>

<script>
	import Bar from './../chart/Bar.js'
	import Pie from './../chart/Pie.js'
	import Radar from './../chart/Radar.js'
	import Line1 from './../chart/Line.js'

	export default {
		components: {
			Bar,
			Pie,
			Radar,
			Line1
	    },
		data() {
			return {
				input: '',
				isPublishExam: 'true',
				tableData: [{
					name: 'C++程序设计',
					start_time: '2016-01-01',
					stop_time: '2016-01-02',
					join: '查看分析'
				}, {
					name: 'JAVA程序设计',
					start_time: '2016-01-01',
					stop_time: '2016-01-02',
					join: '查看分析'
				}]
			}
		},
		created() {
			this.changeTableData()
		},
		watch: {
			"$route": "changeTableData"
		},
		methods: {
			changeTableData() {
				if (this.$route.fullPath == '/orga/publish') {
					this.isPublishExam = 'true'
				} else if (this.$route.fullPath == '/orga/grades') {
					this.isPublishExam = !this.isPublishExam
				}
			}
		}
	}
</script>



