import Vue from 'vue'
import Router from 'vue-router'

import AppNav from '@/components/common/AppNav'
import Template from '@/components/common/Template'

// 教师
import OrgaIntro from '@/components/organization/introduce'
import PublicClass from '@/components/organization/Course'
import TeacherIntro from '@/components/organization/TeacherIntro'
import AccountInfo from '@/components/PersonalInfo/AccountInfo'
import Message from '@/components/PersonalInfo/Message'
import CreateClass from '@/components/organization/CreateClass'
import Train from '@/components/organization/Train'
import HomeWork from '@/components/organization/HomeWork'
import Exam from '@/components/organization/Exam'

// 管理员
import Monitor from '@/components/systemAdmin/Monitor'
import AdminMessage from '@/components/systemAdmin/Message'
import AdminCheck from '@/components/systemAdmin/Check'
import AdminManage from '@/components/systemAdmin/Manage'

// 平台管理
import Department from '@/components/platform/Department'
import PlatformCourse from '@/components/platform/Course'
import PlatformIdentity from '@/components/platform/Identity'
import PlatformCategory from '@/components/platform/Category'
import PlatformInfo from '@/components/platform/PlatformInfo'




Vue.use(Router)

const router = new Router({
    routes: [
        // 教师路由
        {
            path: '/orga',
            name: 'AppNav',
            component: AppNav,
            meta: {
                teacherAuth: true
            },
            children: [{
                    path: 'prga_intro',
                    name: '院校/企业介绍',
                    component: OrgaIntro,
                    meta: {
                        teacherAuth: true
                    },

                },
                {
                    path: 'notice',
                    name: '院校/企业公告',
                    component: TeacherIntro,
                    meta: {
                        teacherAuth: true
                    },
                },
                {
                    path: 'public',
                    name: '公共课程',
                    component: PublicClass,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'private',
                    name: '私有课程',
                    component: PublicClass,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'teacher_intro',
                    name: '教师介绍',
                    component: TeacherIntro,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'comments',
                    name: '评论',
                    component: Template,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'account',
                    name: '教师账号信息',
                    component: AccountInfo,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'message',
                    name: '消息',
                    component: Message,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'createcourse',
                    name: '创建课程',
                    component: CreateClass,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'liveclass',
                    name: '课程直播',
                    component: CreateClass,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'trainlist',
                    name: '实训列表',
                    component: Train,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'traincomments',
                    name: '实训讨论',
                    component: Template,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'hwlist',
                    name: '作业列表',
                    component: HomeWork,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'hwcomments',
                    name: '作业反馈',
                    component: HomeWork,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'publish',
                    name: '发布考试',
                    component: Exam,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'grades',
                    name: '查看成绩',
                    component: Exam,
                    meta: {
                        teacherAuth: true
                    },
                }, {
                    path: 'evaluation',
                    name: '评价',
                    component: Template,
                    meta: {
                        teacherAuth: true
                    },
                },
            ]
        },
        // 平台管理路由
        {
            path: '/platadmin',
            name: 'PlatNav',
            component: AppNav,
            meta: {
                userAuth: true
            },
            children: [{
                path: 'account',
                name: '平台管理员信息',
                component: Template,
                meta: {
                    userAuth: true
                },
            }, {
                path: 'notice',
                name: '消息列表',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'department',
                name: '系别管理',
                component: Department,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'course',
                name: '课程管理',
                component: PlatformCourse,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'identity',
                name: '身份管理',
                component: PlatformIdentity,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'category',
                name: '分类管理',
                component: PlatformCategory,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'advertising',
                name: '宣传管理',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'schoolinfo',
                name: '院校信息管理',
                component: PlatformInfo,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'information',
                name: '资讯管理',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'teacher_superise',
                name: '教师监督',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, {
                path: 'student_superise',
                name: '学生监督',
                component: Template,
                meta: {
                    userAuth: true
                },

            }, ]
        },
        // 系统管理员
        {
            path: '/sysadmin',
            name: 'sysNav',
            component: AppNav,
            meta: {
                adminAuth: true
            },
            children: [{
                    path: 'monitor',
                    name: '直播监控',
                    component: Monitor,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'account',
                    name: '管理员账号信息',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'notice',
                    name: '管理员消息列表',
                    component: AdminMessage,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/basicinfo',
                    name: '院校基本信息管理',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/grade',
                    name: '院校成绩管理',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/student',
                    name: '院校学生管理',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/manage',
                    name: '院校管理',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/join',
                    name: '院校参与度分析',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'school/train',
                    name: '院校实训效果分析',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                },
                {
                    path: 'check',
                    name: '院校审核',
                    component: AdminCheck,
                    meta: {
                        adminAuth: true
                    },
                },
                {
                    path: 'manage',
                    name: '院校管理',
                    component: AdminManage,
                    meta: {
                        adminAuth: true
                    },
                },
                // 企业
                {
                    path: 'orga/basicinfo',
                    name: '企业基本信息管理',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/grade',
                    name: '企业成绩管理',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/student',
                    name: '企业学生管理',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/manage',
                    name: '企业管理',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/join',
                    name: '企业参与度分析',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }, {
                    path: 'orga/train',
                    name: '企业实训效果分析',
                    component: Template,
                    meta: {
                        adminAuth: true
                    },
                }
            ]
        },
        {
            path: '/403',
            name: '403',
            component: Template
        }, {
            path: '*',
            name: '404',
            component: Template
        }
    ]
})

router.beforeEach((to, from, next) => {
    sessionStorage.user_role = 10

    if (to.meta.adminAuth) {
        if (sessionStorage.user_role == 30) {
            next()
        } else {
            next({
                path: '/403'
            })
        }
    } else {
        next()
    }

    if (to.meta.userAuth) {
        if (sessionStorage.user_role == 20) {
            next()
        } else {
            next({
                path: '/403'
            })
        }
    } else {
        next()
    }

    if (to.meta.teacherAuth) {
        if (sessionStorage.user_role == 10) {
            next()
        } else {
            next({
                path: '/403'
            })
        }
    } else {
        next()
    }

    if (to.fullPath == '/' ||
        to.fullPath == '/orga' ||
        to.fullPath == '/orga/' ||
        to.fullPath == '/platadmin' ||
        to.fullPath == '/platadmin/' ||
        to.fullPath == '/sysadmin' ||
        to.fullPath == '/sysadmin/') {

        if (sessionStorage.user_role == 10) {
            next({
                path: '/orga/prga_intro'
            })
        } else if (sessionStorage.user_role == 20) {
            next({
                path: '/platadmin/account'
            })
        } else if (sessionStorage.user_role == 30) {
            next({
                path: '/sysadmin/account'
            })
        } else {
            next({
                path: '/403'
            })
        }
    }


})

export default router;


