<template>
	<div>
		<el-form ref="form" :model="form" label-width="80px"  >
			<el-form-item label="课程名称">
				<el-input v-model="form.name"></el-input>
			</el-form-item>
			<el-form-item label="课程分类">
				<el-select v-model="form.region" placeholder="请选择课程分类">
					<el-option label="区域一" value="shanghai"></el-option>
					<el-option label="区域二" value="beijing"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="开课时间">
				<el-col :span="5">
					<el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
				</el-col>
				<el-col class="line" :span="1" style="margin-left: 35px;">-</el-col>
				<el-col :span="5">
					<el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
				</el-col>
			</el-form-item>
			<el-form-item label="是否私密">
				<el-switch v-model="form.delivery"></el-switch>
			</el-form-item>
			<el-form-item label="开启讨论">
				<el-switch v-model="form.delivery"></el-switch>
			</el-form-item>
			<div v-show="isVideo">
				<el-form-item label="上传视频">
					<el-upload class="upload-demo" drag action="https://jsonplaceholder.typicode.com/posts/" multiple>
						<i class="el-icon-upload"></i>
						<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
					</el-upload>
				</el-form-item>
			</div>
			<div v-show="!isVideo">
				<el-form-item label="推流地址">
					<el-input v-model="input" placeholder="请输入地址"></el-input>
				</el-form-item>
			</div>
			<el-form-item>
				<el-button type="primary">立即创建</el-button>
				<el-button>取消</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				isVideo: 'true',
				input: '',
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
			}
		},
		created () {
			// this.changeTableData()
		},
		watch: {
			"$route": "changeTableData"
		},
		methods: {
			changeTableData() {
				if (this.$route.fullPath == '/orga/createcourse') {
					this.isVideo = 'true'									
				}else if(this.$route.fullPath == '/orga/liveclass'){	
					this.isVideo = !this.isVideo
				}
			}
		}
	}
</script>

<style lang="css" scoped>

</style>


